'use client';

import { useState } from 'react';
import { useRouter } from 'next/navigation';
import { Lock, Mail, Eye, EyeOff, AlertCircle, Loader2 } from 'lucide-react';
import { useAdminAuth } from '@/context/AdminAuthContext';

export default function AdminLoginPage() {
    const router = useRouter();
    const { signIn, signUp, error: authError, loading: authLoading } = useAdminAuth();

    const [email, setEmail] = useState('');
    const [password, setPassword] = useState('');
    const [showPassword, setShowPassword] = useState(false);
    const [error, setError] = useState('');
    const [isSignUp, setIsSignUp] = useState(false);

    const handleSubmit = async (e: React.FormEvent) => {
        e.preventDefault();
        setError('');

        if (password.length < 6) {
            setError('Password must be at least 6 characters');
            return;
        }

        let success: boolean;

        if (isSignUp) {
            success = await signUp(email, password);
        } else {
            success = await signIn(email, password);
        }

        if (success) {
            router.push('/admin/dashboard');
        }
    };

    const displayError = error || authError;

    return (
        <div style={{
            minHeight: '100vh',
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'center',
            background: 'linear-gradient(135deg, #1a1a2e 0%, #16213e 50%, #0f3460 100%)',
            padding: '20px'
        }}>
            <div style={{
                width: '100%',
                maxWidth: '420px',
                backgroundColor: '#fff',
                borderRadius: '24px',
                padding: '48px 40px',
                boxShadow: '0 25px 50px rgba(0,0,0,0.25)'
            }}>
                {/* Logo */}
                <div style={{ textAlign: 'center', marginBottom: '40px' }}>
                    <div style={{
                        width: '72px',
                        height: '72px',
                        background: 'linear-gradient(135deg, #667eea 0%, #764ba2 100%)',
                        borderRadius: '20px',
                        display: 'flex',
                        alignItems: 'center',
                        justifyContent: 'center',
                        margin: '0 auto 20px',
                        boxShadow: '0 10px 30px rgba(102, 126, 234, 0.4)'
                    }}>
                        <Lock size={32} color="#fff" />
                    </div>
                    <h1 style={{ fontSize: '1.6rem', fontWeight: 700, margin: '0 0 8px', color: '#1a1a2e' }}>
                        {isSignUp ? 'Create Admin Account' : 'Admin Portal'}
                    </h1>
                    <p style={{ color: '#888', fontSize: '0.9rem', margin: 0 }}>
                        {isSignUp ? 'Set up your admin credentials' : 'Sign in to Dripzy Dashboard'}
                    </p>
                </div>

                {/* Error Message */}
                {displayError && (
                    <div style={{
                        display: 'flex',
                        alignItems: 'center',
                        gap: '10px',
                        padding: '14px 16px',
                        backgroundColor: '#fef2f2',
                        border: '1px solid #fecaca',
                        borderRadius: '12px',
                        marginBottom: '24px',
                        color: '#dc2626',
                        fontSize: '0.9rem'
                    }}>
                        <AlertCircle size={18} />
                        {displayError}
                    </div>
                )}

                {/* Login Form */}
                <form onSubmit={handleSubmit}>
                    <div style={{ marginBottom: '20px' }}>
                        <label style={{ display: 'block', marginBottom: '8px', fontSize: '0.85rem', fontWeight: 600, color: '#333' }}>
                            Email Address
                        </label>
                        <div style={{ position: 'relative' }}>
                            <Mail size={18} style={{ position: 'absolute', left: '16px', top: '50%', transform: 'translateY(-50%)', color: '#888' }} />
                            <input
                                type="email"
                                value={email}
                                onChange={(e) => setEmail(e.target.value)}
                                placeholder="admin@dripzy.in"
                                required
                                style={{
                                    width: '100%',
                                    padding: '14px 16px 14px 48px',
                                    border: '2px solid #e5e5e5',
                                    borderRadius: '12px',
                                    fontSize: '0.95rem',
                                    outline: 'none',
                                    transition: 'border-color 0.2s',
                                    boxSizing: 'border-box'
                                }}
                            />
                        </div>
                    </div>

                    <div style={{ marginBottom: '28px' }}>
                        <label style={{ display: 'block', marginBottom: '8px', fontSize: '0.85rem', fontWeight: 600, color: '#333' }}>
                            Password
                        </label>
                        <div style={{ position: 'relative' }}>
                            <Lock size={18} style={{ position: 'absolute', left: '16px', top: '50%', transform: 'translateY(-50%)', color: '#888' }} />
                            <input
                                type={showPassword ? 'text' : 'password'}
                                value={password}
                                onChange={(e) => setPassword(e.target.value)}
                                placeholder="â€¢â€¢â€¢â€¢â€¢â€¢â€¢â€¢"
                                required
                                minLength={6}
                                style={{
                                    width: '100%',
                                    padding: '14px 48px 14px 48px',
                                    border: '2px solid #e5e5e5',
                                    borderRadius: '12px',
                                    fontSize: '0.95rem',
                                    outline: 'none',
                                    transition: 'border-color 0.2s',
                                    boxSizing: 'border-box'
                                }}
                            />
                            <button
                                type="button"
                                onClick={() => setShowPassword(!showPassword)}
                                style={{
                                    position: 'absolute',
                                    right: '16px',
                                    top: '50%',
                                    transform: 'translateY(-50%)',
                                    background: 'none',
                                    border: 'none',
                                    cursor: 'pointer',
                                    color: '#888'
                                }}
                            >
                                {showPassword ? <EyeOff size={18} /> : <Eye size={18} />}
                            </button>
                        </div>
                    </div>

                    <button
                        type="submit"
                        disabled={authLoading}
                        style={{
                            width: '100%',
                            padding: '16px',
                            background: authLoading ? '#ccc' : 'linear-gradient(135deg, #667eea 0%, #764ba2 100%)',
                            color: '#fff',
                            border: 'none',
                            borderRadius: '50px',
                            fontSize: '1rem',
                            fontWeight: 600,
                            cursor: authLoading ? 'not-allowed' : 'pointer',
                            display: 'flex',
                            alignItems: 'center',
                            justifyContent: 'center',
                            gap: '10px',
                            boxShadow: authLoading ? 'none' : '0 10px 30px rgba(102, 126, 234, 0.4)',
                            transition: 'all 0.2s'
                        }}
                    >
                        {authLoading ? (
                            <>
                                <Loader2 size={20} style={{ animation: 'spin 1s linear infinite' }} />
                                {isSignUp ? 'Creating Account...' : 'Signing in...'}
                            </>
                        ) : (
                            isSignUp ? 'Create Account' : 'Sign In'
                        )}
                    </button>
                </form>

                {/* Toggle Sign Up / Sign In */}
                <div style={{
                    marginTop: '24px',
                    textAlign: 'center',
                    fontSize: '0.9rem',
                    color: '#666'
                }}>
                    {isSignUp ? (
                        <>
                            Already have an account?{' '}
                            <button
                                onClick={() => setIsSignUp(false)}
                                style={{
                                    background: 'none',
                                    border: 'none',
                                    color: '#667eea',
                                    fontWeight: 600,
                                    cursor: 'pointer'
                                }}
                            >
                                Sign In
                            </button>
                        </>
                    ) : (
                        <>
                            First time?{' '}
                            <button
                                onClick={() => setIsSignUp(true)}
                                style={{
                                    background: 'none',
                                    border: 'none',
                                    color: '#667eea',
                                    fontWeight: 600,
                                    cursor: 'pointer'
                                }}
                            >
                                Create Account
                            </button>
                        </>
                    )}
                </div>

                {/* Info */}
                <div style={{
                    marginTop: '28px',
                    padding: '16px',
                    backgroundColor: '#f0f9ff',
                    border: '1px solid #bae6fd',
                    borderRadius: '12px',
                    fontSize: '0.8rem',
                    color: '#0369a1',
                    textAlign: 'center'
                }}>
                    <strong>ðŸ”’ Secure Admin Access</strong><br />
                    Only authorized email addresses can access the admin panel.
                </div>
            </div>

            <style jsx global>{`
                @keyframes spin {
                    from { transform: rotate(0deg); }
                    to { transform: rotate(360deg); }
                }
            `}</style>
        </div>
    );
}
